.resizing {
  /* margin-left: 30px; */
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.bullet {
  display: "inline-block";
  margin: "0 2px";
  transform: scale(0.8);
}

.resize {
  margin-left: 110px;
}

h5 {
  font-size: 12pt;
}

.media {
  height: 140;
}

.quantity {
  font-size: 15px;
  padding-left: 5px;
  position: relative;
}

.spacing {
  padding-left: 10px;
}

.box {
  background-color: white;
  width: 300px;
  min-height: 300px;
  box-sizing: content-box;
  padding: 25px;
  margin: 25px;
  margin-bottom: 0;
  display: -webkit-flex; /* Safari */
  -webkit-align-items: center; /* Safari 7.0+ */
  display: flex;
  align-items: center;
  cursor: pointer;
}


.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
}

.darken {
  position: relative;
  display: inline-block;
}

.darken:hover:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
}


.shopitem-container {
  /* display: inline-block; */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  align-content: space-between;
}

.shopitem-info-container {
  display: flex;
  /* align-self: center; */
}

